}
.custom-input-block input {
  padding: 10px 15px;
  width: 100%;
  color: #fff;
  font-family: Roboto, sans-serif;
  background-color: #242424;
  border: 2px solid transparent;
  transition: background-color 0.3s ease-in-out;
}
.custom-input-block input::placeholder {
  color: #A2A2A2;
}
.custom-input-block input:focus ~ .bottom {
  transform-origin: bottom left;
  transform: scaleX(1);
}
.custom-input-block input:focus ~ .right {
  transform-origin: bottom right;
  transform: scaleY(1);
}
.custom-input-block input:focus ~ .top {
  transform-origin: top right;
  transform: scaleX(1);
}
.custom-input-block input:focus ~ .left {
  transform-origin: top left;
  transform: scaleY(1);
}
.custom-input-block span {
  position: absolute;
  background-color: #3cefff;
  transition: transform 0.25s ease;
}
.custom-input-block .bottom,
.custom-input-block .top {
  height: 2px;
  left: 0;
  right: 0;
  transform: scaleX(0);
}
.custom-input-block .left,
.custom-input-block .right {
  width: 2px;
  top: 0;
  bottom: 0;
  transform: scaleY(0);
}
.custom-input-block .bottom {
  bottom: 0;
  transform-origin: bottom right;
}
.custom-input-block .right {
  right: 0;
  transform-origin: top right;
  transition-delay: 0.25s;
}
.custom-input-block .top {
  top: 0;
  transform-origin: top left;
  transition-delay: 0.5s;
}
.custom-input-block .left {
  left: 0;
  transform-origin: bottom left;
  transition-delay: 0.75s;
}

