  width: 40vw;
  min-height: 20vw;
  height: max-content;
  padding: 45px 30px 30px;
  position: relative;
  border-radius: 7px;
  background-color: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(4px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1100;
}
.popup__inner p {
  width: 100%;
  text-align: center;
}
.popup__close {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}
.popup__close svg {
  width: 40px;
}
.popup__close svg polygon,
.popup__close svg line {
  transition: 0.3s ease-in-out all;
}
.popup__close:hover svg polygon {
  fill: #3cefff !important;
}
.popup__close:hover svg line {
  stroke: #3cefff !important;
}
.popup.active {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

[class*=transform-],
[class*=ta-] {
  opacity: 0;
  position: relative;
}

.transform-up {
  animation: transformUp 1.2s forwards;
}

.transform-bottom {
  animation: transformBottom 1.2s forwards;
}

.transform-left {
  animation: transformLeft 1.2s forwards;
}

.transform-right {
  animation: transformRight 1.2s forwards;
}

.scaleIn {
  animation: scaleIn 1s forwards;
}

.ta-b {
  animation: textAnimationBottom 2s forwards;
}

.ta-l {
  animation: textAnimationLeft 2s forwards;
}

.t-1 {
  opacity: 0;
  animation-delay: 0.5s;
}

.delay-05 {
  transform: scale(0);
  animation-delay: 0.5s;
}

.t-2 {
  opacity: 0;
  animation-delay: 1.5s;
}

.t-3 {
  opacity: 0;
  animation-delay: 2.5s;
}

.t-4 {
  opacity: 0;
  animation-delay: 3.5s;
}

.t-5 {
  opacity: 0;
  animation-delay: 4.5s;
}

.t-6 {
  opacity: 0;
  animation-delay: 5.5s;
}

.t-7 {
  opacity: 0;
  animation-delay: 6.5s;
}

@keyframes scaleIn {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes transformUp {
  0% {
    opacity: 0;
    transform: translateY(-300px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes transformBottom {
  0% {
    opacity: 0;
    transform: translateY(300px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes transformLeft {
  0% {
    opacity: 0;
    transform: translateX(-300px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes transformRight {
  0% {
    opacity: 0;
    transform: translateX(300px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes textAnimationBottom {
  0% {
    opacity: 0;
    bottom: -50px;
  }
  100% {
    opacity: 1;
    bottom: 0;
  }
}
@keyframes textAnimationLeft {
  0% {
    opacity: 0;
    left: -50px;
  }
  100% {
    opacity: 1;
    left: 0;
  }
}
